import React, { useEffect } from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import _ from 'lodash'

import CTA from '../images/getintouchCTA.svg'

import {TweenLite, Power3} from "gsap/TweenMax";


import { connect } from 'react-redux';

const Container = styled.div`
    width: 10%;
    position: fixed;
    left: 0%;
    margin-left: -5%;
    bottom: 0;

    img {
        width: 100%;
        height: 100%
    }

`


const mapStateToProps = (state) => {
  return {
      isEnglish: state.app.isEnglish
  }
}

let ctaRotation = 0;

let CTARef;

let windowBis;


const CallToAction = ({...props}) => {
  
  let initPageScrollVar = 0;

      useEffect(() => {

        CTARef = document.querySelector('.CTA-image');
        windowBis = document.querySelector('.infinite-scroll');

        windowBis.addEventListener('scroll', _.throttle(handleMenuScroll, 200));

        return () => {

          windowBis.removeEventListener('scroll', _.throttle(handleMenuScroll, 200));

        };
      });

      const handleMenuScroll = () => {

        let initPageScroll = initPageScrollVar;
  
        if(windowBis) {
  
          if(windowBis.scrollTop >= initPageScroll) {

                TweenLite.to(CTARef, 3, {rotation: ctaRotation, x: ctaRotation, ease:Power3.easeOut});
                ctaRotation += 10;

                if(CTARef._gsTransform) {

                    if(CTARef._gsTransform.x > windowBis.clientWidth) {
                        ctaRotation = 0;
                    }
                }
    
          } else if (windowBis.scrollTop <= initPageScroll ) {

            TweenLite.to(CTARef, 3, {rotation: ctaRotation, x: ctaRotation, ease:Power3.easeOut});
            ctaRotation -= 10;
    
          }
    
          return initPageScrollVar = windowBis.scrollTop;
  
        }
    }

    return (
        <Container className="CTA">
            <Link to={props.isEnglish ? '/en/info' : '/info' }><img className="CTA-image" src={CTA}/></Link>
        </Container>
    )
  
}



  export default connect(mapStateToProps)(CallToAction);