/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql, Link } from "gatsby"
import styled from 'styled-components'

const LocaleSwitcher = styled.div`
  position: absolute;
  font-family: 'Calibre-Regular';
  font-size: 34px;
  display: flex;
  flex-direction: column;
  bottom: 0;
  left: 0;
  padding: 1rem;
  z-index: 999;

`

const StyledLink = styled(Link)`
  color: blue;
  text-decoration: none;
  margin-top: 15px;
`


const LocaleContext = React.createContext()

class Layout extends React.PureComponent {

  state = {
    deActive: true,
    enActive: false,
  }

  handleClick(language) {

    if(language === 'de') {
      this.setState({
        deActive: true,
        enActive: false
      });


    } else {
      this.setState({
        deActive: false,
        enActive: true
      })

    }

  }

  render () {

    return(
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={(data, state) => (
          <>
          <>
    
              <LocaleSwitcher data-name="locale-switcher">
                <StyledLink onClick={() => {this.handleClick('de')}} className = {this.state.deActive ? 'active-language' : ''}  hrefLang="de-de" to={"/ziegertreport"}>
                  DE
                </StyledLink>
                <StyledLink onClick={() => {this.handleClick('en')}} className = {this.state.enActive ? 'active-language' : ''}  hrefLang="en-gb" to={"/en/ziegertreport"}>
                  EN
                </StyledLink>
              </LocaleSwitcher>
    
              {this.props.children}
    
          </>
          </>
        )}
      />)

  }
  }

export { LocaleContext, Layout }

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}


