const initialState = {
    isInViewport: null,
    isInfoPage: false,
    isEnglish: false
  };
  
  const TOGGLE_ISINVIEWPORT = 'TOGGLE_ISINVIEWPORT';
  export const toggleIsInViewport = element => ({ type: TOGGLE_ISINVIEWPORT, payload: element });

  const TOGGLE_ISINFOPAGE = 'TOGGLE_ISINFOPAGE';
  export const toggleIsInfoPage = element => ({ type: TOGGLE_ISINFOPAGE, payload: element });

  const TOGGLE_ISENGLISH = 'TOGGLE_ISENGLISH';
  export const toggleIsEnglish = element => ({ type: TOGGLE_ISENGLISH, payload: element });

  
  export default (state = initialState, action) => {
    const { type, payload } = action;
    switch (type) {
      case TOGGLE_ISINVIEWPORT:
        return { ...state, isInViewport: payload };
      case TOGGLE_ISINFOPAGE:
        return { ...state, isInfoPage: payload };
      case TOGGLE_ISENGLISH:
        return {...state, isEnglish: payload};
      default:
        return state;
    }
  };
