import React from "react"
import styled from "styled-components"
import { StaticQuery, graphql } from "gatsby"
import PerfectScrollbar from 'react-perfect-scrollbar'
import { withPrefix } from "gatsby"
import posed from 'react-pose'
import _ from 'lodash'

import {TweenLite, Power2, Power3} from "gsap/TweenMax";

import { ScrollToPlugin } from "gsap/all";

import { connect } from 'react-redux';
 
//or get to the parts that aren't included inside TweenMax:
import Draggable from "gsap/Draggable";
// import ScrollToPlugin from "gsap/ScrollToPlugin";




import StyledLiOdd from './styledliodd'
import StyledLiEven from './styledlieven'


const scrollPlugin = ScrollToPlugin;

const ScrollWrapper = posed.div({
  hide: { y: -100, opacity: 1, transition: { ease: 'easeInOut', duration: 800 } },
  show: { y: 0, opacity: 1, delay: 0, transition: { ease: 'easeInOut', duration: 800 } },
});

const StyledScrollWrapper = styled(ScrollWrapper)`
  position: fixed;
  top: 0;
`


const StyledPerfectScrollbar = styled(PerfectScrollbar)`
  overflow-x: scroll;
  position: relative;
  display: flex;
  width: 100vw;
  top: 15px;
  z-index: 995;
  transform: translateY(-500px);
  
  div:nth-child(1) {
    display: none
  }
`

const mapStateToProps = (state) => {
  return {
      isInfoPage: state.app.isInfoPage,
      isInViewport: state.app.isInViewport,
      isEnglish: state.app.isEnglish
  }
}

class MenuTop extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      menuItems: null,
      isInViewport: null,
      isEnglish: false
    };
    
    this.addElements = this.addElements.bind(this);
    this.handleScrollRight =  _.throttle(this.handleScrollRight.bind(this),500);
    this.handleMenuScroll = _.throttle(this.handleMenuScroll.bind(this), 20);
    this.handleRedirect = this.handleRedirect.bind(this);

    this.initPageScroll = 0;
    this.allMenuItemsInitial = null;
    this.counter = 0;

  }

  componentWillMount(){

    let data = this.props.data.allPrismicPage.edges

    let filteredData = data.filter((item) => item.node.lang === "de-de");


    this.setState({
        menuItems: filteredData
      });

    this.allMenuItemsInitial = filteredData;

  }

  addElements() {
    
    this.setState(prevState => ({
        menuItems: [...prevState.menuItems, ...this.allMenuItemsInitial]
    }));

  }


  handleScrollRight() {

      this.addElements();

      this.counter++;
      


      if(this.counter >= 2) {

        let array = [...this.state.menuItems];
        let arrayLength = this.allMenuItemsInitial.length;

        array.splice(0, arrayLength);

        this.setState({
          menuItems: array
        })

        this.counter = 0;
      }


  }

  isElementInViewport(){

    let menuTopNodes = this.menuRef.children;

    let menuTopNodesArray = Array.from(menuTopNodes);

    for(var i=0; i < menuTopNodesArray.length ; i++) {

      let menuTopNode = menuTopNodesArray[i].children[0].children[0];

      menuTopNode.classList.remove('is-active');

      let inViewport = `${withPrefix(this.props.isInViewport)}/`;

      let currentElement;

      if(this.props.isEnglish) inViewport = `/en${inViewport}`;

      
      if(inViewport === menuTopNodesArray[i].children[0].getAttribute('href') ) {

        menuTopNode.classList.add('is-active');

        if(this.props.isEnglish) {

          inViewport = `/en${withPrefix(this.props.isInViewport)}/`;
  
        } else {
  
          inViewport = `${withPrefix(this.props.isInViewport)}/`;
  
        }


        currentElement = inViewport;

        if( (window.location.pathname !== ( withPrefix('/info') || withPrefix('/info/') )) && (window.location.pathname !== (withPrefix('/en/info') || withPrefix('/en/info/'))) && (window.location.pathname !== currentElement)  ) {

          window.history.pushState(null, null, inViewport);
          
        }

      }
      
    }

  }

  componentDidUpdate(){
    
    this.isElementInViewport();

    setTimeout(() => {
      document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleMenuScroll);
    }, 300);
  }


  componentDidMount(){

    console.log(this.props.isEnglish);

    this.addElements();
    this.addElements();
    
     
    Draggable.create(this.menuRef, {type:"scroll", throwProps:true});
  

    setTimeout(function(){

      TweenLite.to(this.menuRef, 2, {scrollTo:{x: 750}, ease:Power2.easeOut, delay: 1});
      TweenLite.to(this.menuRef, 0.5, {y: 0, ease:Power2.easeOut});

    }.bind(this), 1000);

  }

  handleRedirect(){

    setTimeout(function(){
      document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleMenuScroll);
    }.bind(this),750);

  }

  componentWillReceiveProps() {
    setTimeout(function(){
      document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleMenuScroll);
    }.bind(this),750);
  }

  handleMenuScroll() {

      let initPageScroll = this.initPageScroll;
      let menuScroll = this.menuRef.scrollLeft;
      let window = document.querySelector('.infinite-scroll');

      if(window) {

        if(window.scrollTop >= initPageScroll) {

          TweenLite.to(this.menuRef, 1, {scrollTo:{x: menuScroll + 20}, ease:Power3.easeOut});
          menuScroll ++;
  
        } else if (window.scrollTop <= initPageScroll ) {
  
          TweenLite.to(this.menuRef, 1, {scrollTo:{x: menuScroll - 20}, ease:Power3.easeOut});
          menuScroll--;
  
        }
  
        this.initPageScroll = window.scrollTop;

      }
  }

  


  render() {
    const {menuItems} = this.state;

    let counter = 0;


    return (
      <StyledScrollWrapper pose={this.props.isInfoPage ? 'hide' : 'show'}>
            <StyledPerfectScrollbar onXReachEnd={this.handleScrollRight} containerRef={ div => this.menuRef = div }>
            {menuItems.map((item, index) => {
      
              counter ++;
      
              if (counter % 2 === 0 ) {
                return (
                  <StyledLiEven
                  isEnglish = {this.props.isEnglish}
                  onClick={this.handleRedirect}
                  key={index} 
                  item={item}
                  />
                )
              } else {
                return (
                  <StyledLiOdd
                  isEnglish = {this.props.isEnglish}
                  onClick={this.handleRedirect} 
                  key={index} 
                  item={item}/>
                )
              }
            }
            )}
            </StyledPerfectScrollbar>
      </StyledScrollWrapper>
    )
  }
}


// export default MenuTop

const queryMenuTop = props => (
    <StaticQuery
      query={graphql`
        query {
            allPrismicPage {
                edges {
                  node {
                    lang
                    tags
                    uid
                    data {
                      title {
                        text
                      }
                    }
                  }
                }
              }
        }
      `}
      render={data => (<MenuTop data={data} {...props} />)}
    />
  )


  export default connect(mapStateToProps)(queryMenuTop);